/* Learn page */

.page-id-16 {
  letter-spacing: 0.1rem;

  .landing {
    min-height: 1100px;
    @include desktop {
      min-height: 768px;
    }
  }

  a {
    text-decoration: none;
    color: $white;
  }

  .landing {
    .learn-btn {
      padding: 15px 50px;
      letter-spacing: 0.1rem;
      font-weight: 500;
      font-size: 1.2rem;
      background-color: $light_green;
      border: $light_green;
      box-shadow: 0 4px 4px rgba(12, 11, 11, 0.45);
      border-radius: 5px;
      i {
        margin-left: 10px;
        font-size: 18px;
      }
      @include desktop {
        display: none;
      }
    }
  }

  .landing-wave {
    margin-top: 12rem;
  }

  .landing-title {
    font-size: 3rem;
    font-weight: 300;
  }

  .landing-question {
    margin-top: 10rem;
    @include desktop {
      margin-top: 0;
    }
  }
  .how-to-help {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;

    @include desktop {
      margin-top: 0;
    }
    .how-to-help-title {
      font-family: $font__heading2;
      font-size: 2.8rem;
      padding: 1rem;
      width: 100%;

      @include desktop {
        text-align: center;
        margin-bottom: 1rem;
        margin-top: 3rem;
      }
    }
    .how-to-help-description {
      padding: 1rem;
      line-height: 1.75rem;
      @include desktop {
        align-content: center;
        margin-bottom: 1rem;
      }
    }
  }

  // Shuffle Quotes Grid Section

  .shuffle-quotes-grid {
    position: relative;
    min-height: 92vh;
    font-family: $font__body;
    color: $white;

    .reshuffle-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 80%;
      border-radius: 5px;
      border: none;
      font-size: 1.2rem;
      list-style: none;
      background-color: $light_green;
      font-weight: bold;
      box-shadow: 0 4px 4px rgba(182, 182, 182, 0.75);
      margin: 1rem auto;
      padding: 1rem;

      i {
        margin-right: 1rem;
      }

      @include desktop {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -40%);
        width: 20%;
        height: 30%;

        li {
          font-size: 0.9rem;
        }
        i {
          margin-right: 1rem;
          font-size: 2.8rem;
        }
      }
    }
    ul {
      margin: 0;
      padding: 0;
    }

    .quotes-grid {
      text-align: flex-start;
      display: grid;
      justify-items: center;
      text-align: left;
      grid-template-columns: 1fr;
      grid-gap: 1.5rem;
      padding: 0rem 1.5rem;
      width: 100%;
      margin: 0 auto;

      @include desktop {
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        grid-gap: 0;
        padding: 0;
        grid-gap: 1.8rem;
        margin-bottom: 5rem;
        max-width: 65%;
      }

      li {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 5px;
        margin: 0;
        padding: 2rem;
        font-size: 1.2rem;
        list-style: none;
        background-color: $light_green;
        font-weight: bold;
        display: flex;
        font-size: 1.2rem;
        min-height: 250px;
        line-height: 1.75rem;

        &:nth-child(n + 4) {
          display: none;
        }

        @include desktop {
          &:nth-child(5) {
            visibility: hidden;
          }

          &:nth-child(n + 4) {
            display: flex;
          }
          margin: 0;
        }
      }
    }

    .single-quote {
      align-self: center;
    }
  }
}

//Help Spread Section

.help-spread {
  margin-top: 4rem;
  @include desktop {
    margin-bottom: 2rem;
  }
  h2 {
    font-family: $font__heading2;
    font-size: 1.7rem;
    letter-spacing: 0.1rem;
    font-weight: 200;
    text-align: center;
    @include desktop {
      font-size: 2.8rem;
    }
  }
}

.help-spread-title {
  margin-bottom: 1rem;
  @include desktop {
    text-align: center;
  }
}

.help-template-download {
  text-align: center;
  letter-spacing: 0.1rem;
  font-size: 0.9rem;
  padding: 1rem;
}

// Template Download Section

.template-download {
  height: 100%;
  color: $white;
  line-height: 1.75rem;
  display: block;

  @include desktop {
    display: flex;
  }

  img {
    height: 20px;
    width: 114px;
    align-self: center;
    margin-bottom: 1rem;
  }
  .wallpaper {
    position: relative;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    height: 550px;
    background-size: 664px 1022px;
    border-radius: 5px;
    margin: 1.5rem;
    @include desktop {
      width: 450px;
    }
  }

  .download-btn {
    font-size: 1rem;
    background-color: $light_green;
    border: $light_green;
    box-shadow: 0 4px 4px rgba(12, 11, 11, 0.1);
    border-radius: 50%;
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 1rem 1.3rem;
    i {
      font-size: 35px;
      margin-left: 3px;
    }
  }

  .learn-btn {
    padding: 12px 45px;
    letter-spacing: 0.1rem;
    font-weight: 500;
    font-family: $font__hero-image-sub-heading-description;
    font-size: 1rem;
    background-color: $light_green;
    border: $light_green;
    box-shadow: 0 4px 4px rgba(12, 11, 11, 0.45);
    border-radius: 5px;
    margin-bottom: 3rem;
    align-self: center;
    i {
      margin-left: 10px;
      font-size: 12px;
    }
  }
  h2 {
    font-family: $font__hero-image-sub-heading-description;
    letter-spacing: 0;
  }

  p {
    font-family: $font__hero-image-heading;
    padding-left: 5rem;
    padding-right: 5rem;
    letter-spacing: 0.1rem;
    margin-top: 0;
    @include desktop {
      margin-bottom: 5rem;
    }
  }
}

.template-download .learn-btn {
  @include desktop {
    display: none;
  }
}

.template-download {
  @include desktop {
    flex-direction: row;
  }
}

// Buttons

.wavy-quiz {
  height: 450px;
  width: 100%;
  color: $white;

  h3 {
    width: 300px;
  }

  a {
    text-decoration: none;
  }
  .learn-btn {
    padding: 12px 30px;
    letter-spacing: 0.1rem;
    font-weight: 500;
    font-family: $font__hero-image-sub-heading-description;
    font-size: 1rem;
    background-color: $light_green;
    border: $light_green;
    box-shadow: 0 4px 4px rgba(14, 11, 11, 0.35);
    border-radius: 5px;
    i {
      margin-left: 10px;
      font-size: 12px;
    }
  }
}

// Wavy Newsletter Section

.wavy-newsletter {
  margin: 4rem auto;
  padding: 5rem 0;
  color: $white;
  line-height: 1.5;

  .wavy-newsletter-title {
    font-weight: 300;
    width: 80%;
    font-size: 2.5rem;

    @include desktop {
      font-weight: 600;
      font-size: 3.5rem;
    }
  }
}
