.error404 {
  background: url(http://refilter.van.cp.academy.red/wp-content/uploads/2019/09/hero-404-desktop.png);
  background-size: cover;
  height: 100vh;
  background-repeat: no-repeat;

  .content {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $white;

    p {
      font-size: 2rem;
    }

    .contact-us {
      color: $light_green;
    }
  }
}
